@import url(http://fonts.googleapis.com/css?family=Open+Sans:700,300,600,800,400);
html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

body {
  text-align: center;
}

header .navbar{
  z-index: 10;

  border: 0;
}

#banner {
  background: url(../img/background.jpg);
  width: 100%;
  height: 800px;
  position: fixed;
  right: 0;
}

#content {
  top: 800px;
  width: 100%;
  position: relative;
  background: #ebebeb;
  height: 1500px;
}

.logincontainer, .registercontainer{
  margin-top: 60px;
}

